
.forgotpassword-btn{
  margin-top: 10px;
  transition: .2s;
  color: rgb(129, 194, 255);
}
.forgotpassword-btn:hover{
  color: rgb(1, 255, 192);
  letter-spacing: 1px;
  cursor: pointer;
}
.signupredirect-link{
  text-decoration: none;
  transition: .2s;
  color: rgb(129, 194, 255);
}
.signupredirect-link:hover{
  color: rgb(1, 255, 192);
  letter-spacing: 2px;
}
.loginredirect-link{
  text-decoration: none;
  transition: .2s;
  color: rgb(129, 194, 255);
}
.loginredirect-link:hover{
  color: rgb(1, 255, 192);
  letter-spacing: 2px;
}


.bruhmain{
  position: fixed; 
  z-index: 10;
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%; 
  background-color: rgba(0, 0, 0, 0.61);
}

.content {
  background-color: #020014;
  width: 75%;
  border-radius: 10px;
  height: 60%;
  margin-top: calc(5vw + 80px);
}

.close {
  color: rgb(255, 255, 255);
  float: right;
  position: relative;
  font-size: 30px;
  right: 10px;
  font-weight: bold;
}
.close:hover{
  color: rgb(0, 81, 255);
  text-decoration: none;
  cursor: pointer;
}

.passwordreset-email-form-main{
  display: flex;
  flex-direction: column;
}

h2{
  font-size: calc(1vw + 13px);
  margin-top: calc(5vw + 60px);
  margin-top: calc(30vh - 120px);
  max-width: 800px;
  padding: 0px 10px;
  margin-bottom: 20px;
}

.password-reset-email{
  width: 420px;
  height: 45px;
  border-radius: 5px;
  background: white;
  color: rgb(0, 0, 0);
  transition: .1s;
  font-size: 18px;
  margin-top: 20px;
}
.password-reset-email:focus{
  background: rgb(46, 0, 73);
  color: rgb(190, 190, 190);
}

.emailform-button{
  height: calc(1vw + 30px);
  width: calc(2vw + 130px);
  color: white;
  background: transparent;
  border: 1px solid white;
  cursor: pointer;
  font-size: calc(.2vw + 15px);
  margin-top: 20px;
  transition: .2s;
  border-radius: 6px;
}
.emailform-button:hover{
  background: white;
  color: #020014;
}

.emailform-infomessage{
  margin-top: calc(15vh - 70px);
  color: rgb(27 255 0);
  font-size: calc(.3vw + 15px);
  position: relative;
  font-family: monospace;
}
@media screen and (max-width: 800px){
  .password-reset-email{
    width: calc(50vw + 30px);
    height: calc(2vw + 18px);
    font-size: calc(1vw + 12px);
    

  }
}
@media screen and (max-width: 650px){
  .password-reset-email{
    margin-left: calc(6vw - 1px);
  }
  .emailform-button{
    font-size: calc(.2vw + 12px);
  }
}